<template>
    <div>
        <nav-bar :name="title"></nav-bar>

        <v-spacer></v-spacer>
        
        <v-card class="ma-1" cols="12">
        <v-container>
            <v-row>
                <v-col cols="12">
                    <h5>*File</h5>
                <v-file-input
                label="Choose file"
                outlined
                dense
                @change="selectFile"
                ></v-file-input>
                    <h6>This field is required.</h6>
                </v-col>
            </v-row>
            <v-col>
            <v-btn
            class="mx-4 my-2"
            color="#989898"
            dark
            @click="$router.go(-1)"
            >
            GO BACK
            </v-btn>
            <v-btn
            class="mx-4 my-2 primary"
            light
            :loading="loading"
            @click="add_sales"
            >
            ADD REGISTER
            </v-btn>
            </v-col>
        </v-container>
        </v-card>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import SalesService from '@/services/SalesService';
import Swal from 'sweetalert2';

export default {
    components: {
      NavBar
    },

    data: () => ({
        title: 'ADD SALE',
        loading: false,
        file: undefined
    }),

    methods:{
        add_sales(){
            this.loading = true
            SalesService
                .add_sales(this.file)
                .then(res => {
                    Swal.fire({
                    icon: 'success',
                    title: 'Success',
                    text: res['message']
                    }).then(() => {
                        this.loading = false
                        this.$router.go(-1)
                    })
                })
                .catch(e => {
                        /* 
                        *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                        */
                    let error = e.response.data;
                    let msg = ''
                    Object.entries(error).forEach(([ key, value ]) => {
                        msg+= key + ': ' + value + ' \n ';
                    });
                        this.loading = false;
                        Swal.fire({
                        icon: 'error',
                        title: 'Error',
                        text: msg
                        });
                    })
        },

        /* METODO PARA PROCESAR EL PDF DEL INPUT */
        selectFile(file){
            this.file = file;
            console.log(this.file)
        },
    }
}
</script>